import { Component, OnInit } from '@angular/core';

import { jpaEntity } from '../jpa-entity'
import { jdk } from '../jdk'

@Component({
  selector: 'app-kit',
  templateUrl: './kit.component.html',
  styleUrls: ['./kit.component.scss']
})
export class KitComponent implements OnInit {

  constructor() { }

  ngOnInit() {
    this.getShowData('jpa-entity');
  }

  private showData : string = '';

  private jpaEntityShow : boolean = false;
  getShowData(name){
    this.initShow();
      switch(name){
        case 'jpa-entity':
        this.showData = jpaEntity;this.jpaEntityShow = true;break;
        case 'jdk':
        this.showData = jdk;break;
      }
  }

  initShow(){
    this.jpaEntityShow = false;
  }

}
