import { Routes } from '@angular/router';

import { SplibComponent } from '../bulu/middle.troops/java/springboot/splib/splib.component'
import { SpdetailComponent } from '../bulu/middle.troops/java/springboot/spdetail/spdetail.component'

import { DistributedComponent } from '../bulu/middle.troops/java/distributed/distributed/distributed.component'
import { KitComponent } from '../bulu/middle.troops/java/toolkit/kit/kit.component'

export const javaRoue : Routes = [
    {
        path:'java/lib',
        component:SplibComponent,
        children:[
            {path:'detail',component:SpdetailComponent}
        ]
    },
    {
        path:'distributed',
        component: DistributedComponent
    },
    {
        path:'java/kit',
        component: KitComponent
    }
]