import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { domain } from '../domain'
import { interceptor } from '../interceptor'
import { shiro } from '../shiro'
import { shiroRealm } from '../shiro.realm'
import { shiroConfig } from '../shiro.config'
import { exception } from '../exception'

@Component({
  selector: 'app-splib',
  templateUrl: './splib.component.html',
  styleUrls: ['./splib.component.scss']
})
export class SplibComponent implements OnInit {

  constructor(private route : Router,private routerinfo : ActivatedRoute) { }

  ngOnInit() {
    this.routerinfo.queryParams.subscribe(queryParams =>{
      this.goDeatil(queryParams.opName)
    })
  }

  private showData : string;
  private part1 : string;
  private part2 : string;
  private part3 : string;
  private shiroShow : boolean = false;

  goDeatil(name){
    this.initShow();
    switch(name){
      case 'domain':
      this.showData = domain;break;
      case 'interceptor':  
      this.showData = interceptor;break;
      case 'shiro':
      this.part1 = shiro;
      this.part2 = shiroRealm;
      this.part3 = shiroConfig;
      this.shiroShow = true;
      this.showData = '';break;
      case 'exception':
      this.showData = exception;break;
      default:
      this.showData = domain;
    }
  }

  initShow(){//初始化页面上的**show
       this.shiroShow = false;
       this.part1 = '';
       this.part2 = '';
       this.part3 = '';
  }

}
