import { Routes } from '@angular/router';

import { NglibComponent } from '../bulu/middle.troops/web/angular/nglib/nglib.component'
import { NgbaseComponent } from '../bulu/middle.troops/web/angular/ngbase/ngbase.component'
import { BasicuseComponent } from '../bulu/middle.troops/web/angular/basicuse/basicuse.component'
import { LeadinComponent } from '../bulu/middle.troops/web/angular/leadin/leadin.component'

import { VuelibComponent } from '../bulu/middle.troops/web/vue/vuelib/vuelib.component'
import { VuebaseComponent } from '../bulu/middle.troops/web/vue/vuebase/vuebase.component'
import { VuerouteComponent } from '../bulu/middle.troops/web/vue/vueroute/vueroute.component'
import { VueaxiosComponent } from '../bulu/middle.troops/web/vue/vueaxios/vueaxios.component'

export const webRoute : Routes = [
    {
        path:'angular/nglib',
        component:NglibComponent,
        children:[
            {path:'angular/ngbase',component:NgbaseComponent},
            {path:'angular/basicuse',component:BasicuseComponent},
            {path:'angular/leadin',component:LeadinComponent},
        ]
    },
    {
        path:'vue/lib',
        component:VuelibComponent,
        children:[
            {path:'vue/base',component:VuebaseComponent},
            {path:'routeuse',component:VuerouteComponent},
            {path:'axios',component:VueaxiosComponent}
        ]
    }
]