import { Component, OnInit } from '@angular/core';

import { dubbo } from '../dubbo'
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-distributed',
  templateUrl: './distributed.component.html',
  styleUrls: ['./distributed.component.scss']
})
export class DistributedComponent implements OnInit {

  constructor(private route : Router,private routeinfo : ActivatedRoute) { }

  ngOnInit() {
    this.routeinfo.queryParams.subscribe(queryParams =>{
      this.goDetail(queryParams.opName);
    })
  }

  private showData : string = dubbo;//赋初始值显示dubbo + zookeeper分布式架构内容
  private dubboShow : boolean = true;
  
  goDetail(name){
    this.controlShow();
      switch(name){
        case 'dubbo':
        this.showData = dubbo;this.dubboShow = true;break;
        default:
        this.showData = dubbo;this.dubboShow = true;break;
      }
  }

  goOtherLib(path,name){
     this.route.navigate([path],{
       queryParams:{
         opName:name
       }
     })
  }

  //所有ngIf控制的内容全部隐藏全部隐藏
  controlShow(){
      this.dubboShow = false;
  }

}
