import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-bodyer',
  templateUrl: './bodyer.component.html',
  styleUrls: ['./bodyer.component.scss']
})
export class BodyerComponent implements OnInit {

  constructor(private router : Router) { }

  ngOnInit() {
    this.router.navigate(['body/homepage']);
   }

  goDirectory(dirName){//切换到左侧显示目录
      this.router.navigate(['directory'],{
        queryParams:{
          dirName:dirName
        }
      }) 
  }

}
